import * as React from "react";
import Divider from '@mui/material/Divider';
import {Button, Collapse, Div} from "atomize";
import {useNavigate} from "react-router-dom";

export default function CollapseUser (props) {
    const navigate = useNavigate();

    const DestoryUser = () => {
        localStorage.removeItem('user');
        localStorage.removeItem('uuid');
        localStorage.removeItem('guilds');
        navigate('/');
    }

    const User = JSON.parse(localStorage.getItem('user') || '{}');
    const [showCollapse, setShowCollapse] = React.useState(false);

    /*React.useEffect(()=>{
        document.addEventListener("mousedown", ()=>showCollapse ? setShowCollapse(false) : void(0));
    }, []);*/

    return (
        <>
            {
                User.id ?
                    <>
                    <img src={User.avatarURL} width={'40rem'} height={'40rem'} style={{borderRadius:'20rem'}} onClick={() =>setShowCollapse(!showCollapse)}/>
                        <Collapse isOpen={showCollapse} pos="fixed" m={{t:'3rem'}}>
                            <Div
                                bg="gray100"
                                border="1px solid"
                                borderColor="gray400"
                                rounded="lg"
                                align="flex-start"
                            >
                                <Div
                                    p={{ x: "1rem", y: "0.75rem" }}
                                    border={{ b: "1px solid" }}
                                    borderColor="gray400"
                                    style={{minWidth:'10rem',color:'black'}}
                                >
                                    {User.id ? `${User.username}#${User.discriminator}` : 'Nie ma'}
                                </Div>
                                <Divider/>
                                <Div
                                    p={{ x: "1rem", y: "0.75rem" }}
                                    border={{ b: "1px solid" }}
                                    borderColor="gray400"
                                    style={{minWidth:'10rem',color:'black'}}
                                >
                                    <a onClick={()=>DestoryUser()}>Wyloguj się</a>
                                </Div>
                            </Div>
                        </Collapse>
                        <a>˅</a>
                    </>
                    :
                    <Button
                        onClick={()=>navigate('/auth')}
                    >
                        Log in
                    </Button>
            }
        </>
    );
}
