import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MailIcon from '@mui/icons-material/Mail';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';

import {useNavigate} from 'react-router-dom';



import { Row, Col, Div, currentDevice, Text, Container, Collapse, Button } from 'atomize';
import {createTheme, ThemeProvider, } from "@mui/material";

import CollapseUser from '../Components/collapse/CollapseUser';

const theme = createTheme({
    typography: {
        fontFamily: [
            'Public Sans',
        ].join(','),
    },
    components: {
        MuiDrawer: {
            styleOverrides: {
                paper: {
                    backgroundColor: "#f9fafb",
                    color: "#637381",
                    borderRight: '1px dashed',
                    borderColor: 'rgba(183,183,183,0.22)'
                },
            }
        }
    },
});

const drawerWidth = 280;

export default function GridPage(props) {
    const navigate = useNavigate();
    const { window, active } = props;
    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const drawer = (
        <div>
            <Div h="5rem" style={{display:'flex',flexDirection:'row',justifyContent:'left',alignItems:'center',paddingLeft: '1rem'}}>
                <img src={'https://assistantscenter.com/img/favicon.png'} style={{width:'3rem',height:'3rem',borderRadius:'3rem'}}/>
                <Text tag="h1" textSize="subheader" m={{ r: '1rem', l: '1rem', }}></Text>
            </Div>
            <List sx={{
                // selected and (selected + hover) states
                '&&.MuiListItem-root, && .MuiListItem-root:hover': {
                    bgcolor: 'rgb(245,245,245)',
                    '&, & .MuiListItemIcon-root': {
                        color: '#cf24e3',
                    },
                },
                // hover states
                '&&.MuiListItem-root, && .Mui-selected': {
                    bgcolor: 'rgb(245,245,245)',
                    '&, & .MuiListItemIcon-root': {
                        color: '#cf24e3',
                    },
                },
            }}>
                {[
                    {
                        title: 'Home',
                        href: '/'
                    },
                    {
                        title: 'Dashboard',
                        href: '/dashboard'
                    },
                    ].map((info, index) => (
                    <Div style={{padding:'0.1rem'}}>
                        <ListItem selected={info.title.toLowerCase()==active} button key={info.title} style={{
                            borderRadius: "0.9em",
                            height: '2.8rem',
                            "&,&:focus": {
                                backgroundColor: "#F6F6E8",
                                textDecoration: "line-through"
                            },
                            "&:hover": {
                                textDecoration: "line-through",
                                backgroundColor: 'red'
                            }
                        }} onClick={()=>navigate(info.href)}>
                            <ListItemIcon style={{minWidth: '45px'}}>
                                <InboxIcon />
                            </ListItemIcon>
                            <Text textSize={'paragraph'} textWeight={'500'}>{info.title}</Text>
                        </ListItem>
                    </Div>
                ))}
            </List>
        </div>
    );

    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <ThemeProvider theme={theme}>
            <Box sx={{ display: 'flex' }}>
                <CssBaseline />
                <AppBar
                    position="fixed"
                    style={{
                        background: 'rgba(249,250,251,0.75)',
                        backdropFilter: 'blur(4px)',
                        borderColor: 'red'
                    }}
                    sx={{
                        width: { sm: `calc(100% - ${drawerWidth}px)` },
                        ml: { sm: `${drawerWidth}px` },
                        boxShadow: 0,
                        borderColor: 'red'
                    }}
                >
                    <Toolbar sx={{
                        height: '5rem'
                    }}>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{ mr: 2, display: { sm: 'none' } }}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Div style={{width:'100%',display:'flex',justifyContent:'space-between'}}>
                            <Typography variant="h6" noWrap component="div" color={'black'}>
                                Responsive drawer xds
                            </Typography>
                            <Typography sx={{ display: {
                                    xs: 'none',
                                    sm: 'none',
                                    md: 'flex',
                                    lg: 'flex',
                                    xl: 'flex'
                                } }} style={{width:'200px',justifyContent:'flex-end'}}>
                                <CollapseUser/>
                            </Typography>
                        </Div>
                    </Toolbar>
                </AppBar>
                <Box
                    component="nav"
                    sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 }, borderColor: 'red' }}
                    aria-label="mailbox folders"
                >
                    {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                    <Drawer
                        container={container}
                        variant="temporary"
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                        sx={{
                            display: { xs: 'block', sm: 'none' },
                            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
                            borderColor: 'red'
                        }}
                    >
                        {drawer}
                    </Drawer>
                    <Drawer
                        variant="permanent"
                        sx={{
                            display: { xs: 'none', sm: 'block' },
                            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
                        }}
                        open
                    >
                        {drawer}
                    </Drawer>
                </Box>
                <Box
                    component="main"
                    sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
                >
                    <Toolbar style={{height:'90px'}} />
                    <Container>
                        {
                            props.children
                        }
                    </Container>
                </Box>
            </Box>
        </ThemeProvider>
    );
}
